<template>
  <div id="container">
    <div id="header">
      <el-button size="mini" style="margin:5px auto" type="info" icon="el-icon-back" @click ="backToHall"></el-button>
    </div>
    <div id="display">
      <div id="aside">
        <player v-for="player in players" :key="player.seat" v-bind="player"></player>
      </div>
      <div id="drawTool" v-if="this.state==1">

        <div class = "toolLine">
          <div class = "tool" style="background-image:url(/铅笔.svg)" @click="this.tool='pencil'">
          </div>
          <div class = "tool" style="background-image:url(/拾色器.svg)"  @click="this.tool='colorGetter'">
          </div>
        </div>
        
        <div class = "toolLine">
          <div class = "tool" style="background-image:url(/实心圆.svg)"  @click="this.tool='filledCircle'">
          </div>
          <div class = "tool" style="background-image:url(/空心圆.svg)"  @click="this.tool='circle'">
          </div>
        </div>
        <div class = "toolLine">
          <div class = "tool" style="background-image:url(/实心矩形.svg)"  @click="this.tool='filledRectangular'">
          </div>
          <div class = "tool" style="background-image:url(/空心矩形.svg)"  @click="this.tool='rectangular'">
          </div>
        </div>

        <div class = "toolLine">
          <div class = "tool" style="background-image:url(/橡皮擦.svg)"  @click="this.tool='eraser'">
          </div>
          <div class = "tool" style="background-image:url(/清空.svg)"  @click="this.ws.send(JSON.stringify({command:'Draw',content:{type:'clear'}}));">
          </div>
        </div>

        <div class = "colorLine">
          <div class = "tool" style="background-color:black" @click="this.color='#000000'"></div>
          <div class = "tool" style="background-color:aqua" @click="this.color='#00FFFF'"></div>
          <div class = "tool" style="background-color:blue" @click="this.color='#0000FF'"></div>
          <div class = "tool" style="background-color:brown" @click="this.color='#A52A2A'"></div>
          <div class = "tool" style="background-color:yellow" @click="this.color='#FFFF00'"></div>
          <div class = "tool" style="background-color:red" @click="this.color='#FF0000'"></div>
          <div class = "tool" style="background-color:green" @click="this.color='#008000'"></div>
        </div>
        
        <div class = "colorLine">
          <div class = "tool" style="background-color:orange" @click="this.color='#FFA500'"></div>
          <div class = "tool" style="background-color:purple" @click="this.color='#800080'"></div>
          <div class = "tool" style="background-color:azure" @click="this.color='#F0FFFF'"></div>
          <div class = "tool" style="background-color:fuchsia" @click="this.color='#FF00FF'"></div>
          <div class = "tool" style="background-color:gray" @click="this.color='#808080'"></div>
          <div class = "tool" style="background-color:silver" @click="this.color='#C0C0C0'"></div>
          <div class = "tool" style="background-color:tomato" @click="this.color='#FF6347'"></div>
        </div>

        <div class = "colorLine">
          <input id="colorChooser" type="color" class="tool" v-model="color">
        </div>

        <div class="colorLine">
          <div class="tool">
            <el-slider v-model="penSize" :min="1" :max="50"></el-slider>
          </div>
        </div>

        <div class="colorLine">
          <div class="tool">
            <el-slider v-model="opcacity" :max="255"></el-slider>
          </div>
        </div>

      </div>
      <div id="mainContainer">
        <div id="canvasContainer">
          <div id="problem" v-if="this.state==2||this.state==1">{{this.problem}}</div>
          <canvas id="myCanvas" @mousedown="down($event)" @mousemove="moveEvent?moveEvent($event):null" @mouseup="up($event)" @mouseout="up($event)"></canvas>
          <progress v-if="this.state==2||this.state==1" id="progress" :value="progress" max="60000"></progress>
          <div class="turnEndContent" v-if="((this.state==0||this.state==3) && this.observer == false)||this.waiting==true">
            <el-button v-if="this.state==0 && isOwner" id="startButton" type="primary" @click="startGame">游戏开始</el-button>
            <div v-if="this.state==3" style="position:relative;top:80%;margin:10px auto;width:40%;height:10%;line-height:50px;text-align:center">
              {{this.turnEndMessage}}
            </div>
          </div>
        </div>
        <div id="messageContainer">
          <div class="answer">
            <div class="history" id="answerHistory">
            </div>
            <input class="sendMessage" :disabled="this.answerDisable || this.waiting" v-model="this.answerInput" @keyup="sendAnswer($event)" placeholder="在这里输入答案">
          </div>
          <div class="chat">
            <div class="history" id="chatHistory">
            </div>
            <input class="sendMessage" v-model="this.chatInput" @keyup="sendChat($event)" placeholder="在这里输入聊天内容">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import player from "./components/Player.vue"


export default {
  methods:{
    startGame:function(){
      this.ws.send(JSON.stringify({
        command:"StartGame",
        content:null
      }));
    },
    down:function(event){
      if(this.state != 1)
        return;
      this.drawBeginX=event.offsetX;
      this.drawBeginY=event.offsetY;
      this.moveEvent = this.move;
      this.context.lineWidth=this.penSize;
      switch(this.tool)
      {
        case "colorGetter":
          this.getColor(event);
          break;
        case "pencil":
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"setLine",
              color:this.hexToRgba(this.color).rgba,
              lineWidth:this.penSize
            }
          }));
          break;
        case "eraser":
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"setColor",
              color:this.hexToRgba("#FFFFFF").rgba,
              lineWidth:this.penSize
            }
          }));
          break;
        case "filledCircle":
        case "circle":
        case "filledRectangular":
        case "rectangular":
          this.imageData = this.context.getImageData(0,0,this.canvas.width,this.canvas.height);
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"setColor",
              color:this.hexToRgba(this.color).rgba
            }
          }));
          break;
      }
    },
    up:function(event){
      if(this.state != 1)
      return;
      if(this.moveEvent == null)
        return;
      this.moveEvent = null;
      var x,y,radius;
      switch(this.tool)
      {
        case "colorGetter":
          this.getColor(event);
          break;
        case "pencil":
        case "eraser":
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"drawLine",
              beginX:this.drawBeginX,
              beginY:this.drawBeginY,
              endX:event.offsetX,
              endY:event.offsetY
            }
          }));
          break;
        case "filledCircle":
          this.context.putImageData(this.imageData,0,0);
          x = (this.drawBeginX+event.offsetX)/2;
          y = (this.drawBeginY+event.offsetY)/2;
          radius = Math.abs(this.drawBeginX-event.offsetX)/2;
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"drawFilledCircle",
              X:x,
              Y:y,
              r:radius
            }
          }));
          break;
        case "circle":
          this.context.putImageData(this.imageData,0,0);
          x = (this.drawBeginX+event.offsetX)/2;
          y = (this.drawBeginY+event.offsetY)/2;
          radius = Math.abs(this.drawBeginX-event.offsetX)/2;
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"drawCircle",
              X:x,
              Y:y,
              r:radius
            }
          }));
          break;
        case "filledRectangular":
          this.context.putImageData(this.imageData,0,0);
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"drawFilledRectangular",
              X1:event.offsetX,
              Y1:event.offsetY,
              X2:this.drawBeginX,
              Y2:this.drawBeginY
            }
          }));
          break;
        case "rectangular":
          this.context.putImageData(this.imageData,0,0);
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"drawRectangular",
              X1:event.offsetX,
              Y1:event.offsetY,
              X2:this.drawBeginX,
              Y2:this.drawBeginY
            }
          }));
          break;
      }
    },
    move:function(event){
      if(this.state != 1)
        return;
      var x,y,radius;
      switch(this.tool)
      {
        case "colorGetter":
          this.getColor(event);
          break;
        case "pencil":
        case "eraser":
          this.ws.send(JSON.stringify({
            command:"Draw",
            content:{
              type:"drawLine",
              beginX:this.drawBeginX,
              beginY:this.drawBeginY,
              endX:event.offsetX,
              endY:event.offsetY
            }
          }));
          this.drawBeginX = event.offsetX;
          this.drawBeginY = event.offsetY;
          break;
        case "filledCircle":
          this.context.putImageData(this.imageData,0,0);
          x = (this.drawBeginX+event.offsetX)/2;
          y = (this.drawBeginY+event.offsetY)/2;
          radius = Math.abs(this.drawBeginX-event.offsetX)/2;
          this.context.beginPath();
          this.context.arc(x,y,radius,0,2*Math.PI);
          this.context.fill();
          this.context.closePath();
          break;
        case "circle":
          this.context.putImageData(this.imageData,0,0);
          x = (this.drawBeginX+event.offsetX)/2;
          y = (this.drawBeginY+event.offsetY)/2;
          radius = Math.abs(this.drawBeginX-event.offsetX)/2;
          this.context.beginPath();
          this.context.arc(x,y,radius,0,2*Math.PI);
          this.context.stroke();
          this.context.closePath();
          break;
        case "filledRectangular":
          this.context.putImageData(this.imageData,0,0);
          x = Math.min(this.drawBeginX,event.offsetX);
          y = Math.min(this.drawBeginY,event.offsetY);
          this.context.beginPath();
          this.context.fillRect(x,y,Math.abs(this.drawBeginX-event.offsetX),Math.abs(this.drawBeginY - event.offsetY));
          this.context.closePath();
          break;
        case "rectangular":
          this.context.putImageData(this.imageData,0,0);
          x = Math.min(this.drawBeginX,event.offsetX);
          y = Math.min(this.drawBeginY,event.offsetY);
          this.context.beginPath();
          this.context.rect(x,y,Math.abs(this.drawBeginX-event.offsetX),Math.abs(this.drawBeginY - event.offsetY));
          this.context.stroke();
          this.context.closePath();
          break;
      }
    },
    hexToRgba:function(hex) {
    var rgba="rgba("+parseInt("0x" + hex.slice(1, 3))+','+parseInt("0x" + hex.slice(3, 5))+','+parseInt("0x" + hex.slice(5, 7))+','+(this.opcacity)/255+')';
    return {
        red: parseInt("0x" + hex.slice(1, 3)),
        green: parseInt("0x" + hex.slice(3, 5)),
        blue: parseInt("0x" + hex.slice(5, 7)),
        rgba:rgba
    }
    },
    getColor:function(event){
      var c = this.context.getImageData(event.offsetX, event.offsetY, 1, 1).data;
      this.color="#"+(c[0].toString(16)).padStart(2,"0")+(c[1].toString(16)).padStart(2,"0")+(c[2].toString(16)).padStart(2,"0");
      this.opcacity = c[3];
    },
    sendAnswer:function(event){
      if(event.keyCode == "13" && this.answerInput !="")
      {
        this.ws.send(JSON.stringify({
          command:"Guess",
          content:this.answerInput
        }));
        this.answerInput="";
      }
    },
    sendChat:function(event){
      if(event.keyCode == "13" && this.chatInput !="")
      {
        this.ws.send(JSON.stringify({
          command:"Talk",
          content:this.chatInput
        }));
        this.chatInput="";
      }
    },
    backToHall:function(){
      window.location.href='/hall';
    }
  },
  data(){
    return {
      isOwner:false,
      playerNum:0,
      players:[],
      rounds:0,
      ws:undefined,
      problem:"",
      hints:[],
      color:"#000000",
      penSize:5,
      tool:"pencil",
      state:0, //0 未开始,1 画画轮,2 猜测轮,3 两轮之间
      canvas:null,
      context:null,
      drawBeginX:null,
      drawBeginY:null,
      drawEndX:null,
      drawEndY:null,
      opcacity:255,
      moveEvent:null,
      imageData:null,
      blankImageData:null,
      answerInput:"",
      chatInput:"",
      answerHistory:null,
      chatHistory:null,
      turnEndMessage:"",
      answerDisable:true,
      progress:0,
      endTime:0,
      observer:false,
      waiting:false
    }
  },
  name: 'App',
  components: {
    player
  },
  mounted:function(){
    this.canvas=document.getElementById('myCanvas');
    this.answerHistory=document.getElementById('answerHistory');
    this.chatHistory=document.getElementById('chatHistory');

    this.context=this.canvas.getContext('2d');

    this.canvas.height = 500;
    this.canvas.width = 900;
    this.context.fillStyle='#FFFFFF';
    this.context.fillRect(0,0,this.canvas.width,this.canvas.height);
    this.context.lineCap = 'round';

    this.blankImageData = this.context.getImageData(0,0,this.canvas.width,this.canvas.height);

    this.ws = new WebSocket('ws://'+window.location.host+'/game');
    this.ws.addEventListener('open',event=>{
        if(event.data == "beOwner")
        {
          this.isOwner = true;
        }
    });
    
    this.ws.addEventListener('message', event=>{
        var msgs = JSON.parse(event.data);
        var para ,node;
        var x1,y1,x2,y2,x,y;
        if(msgs.command == "BeOwner")
        {
          this.isOwner = true;
          return ;
        }
        else if(msgs.command == "BeObserver")
        {
          para = document.createElement("p");
          node = document.createTextNode('房间玩家已满,以旁观者身份进入');
          para.appendChild(node);
          this.answerHistory.appendChild(para);
          this.answerHistory.scrollTop = this.answerHistory.scrollHeight;
          this.observer=true;
          return ;
        }
        else if(msgs.command == "BePlayer")
        {
          this.observer = false;
          para = document.createElement("p");
          node = document.createTextNode('你成为了玩家');
          para.appendChild(node);
          this.answerHistory.appendChild(para);
          this.answerHistory.scrollTop = this.answerHistory.scrollHeight;
          if(this.state == 2)
            this.answerDisable = false;
          return;
        }
        else if(msgs.command == "UpdatePlayers")
        {
          this.players = msgs.content;
          return;
        }
        else if(msgs.command == "DrawTurnBegin")
        {
          this.problem = msgs.content;
          this.waiting = false;
          this.answerDisable = true;
          this.state=1;
          this.endTime = Date.now() + 60000;
          this.progress = this.endTime - Date.now();
          clearInterval();
          setInterval(()=>{
            this.progress = this.endTime - Date.now();
            if(this.progress == 0)
              clearInterval();
          },100);
          return;
        }
        else if(msgs.command == "GuessTurnBegin")
        {
          this.problem = "_ ".repeat(msgs.content);
          this.waiting = false;
          this.state=2;
          if(this.observer == false)
            this.answerDisable = false;
          this.endTime = Date.now() + 60000;
          this.progress = this.endTime - Date.now();
          clearInterval();
          setInterval(()=>{
            this.progress = this.endTime - Date.now();
            if(this.progress == 0)
              clearInterval();
          },100);
          return;
        }
        else if(msgs.command == "TurnEnd")
        {
          this.context.putImageData(this.blankImageData,0,0);
          this.state = 3;
          this.answerDisable = true;
          if(msgs.content == null)
          {
            this.turnEndMessage = "本轮结束，所有人回答正确";
          }
          else
          {
            this.turnEndMessage = "本轮结束,"+msgs.content +"人未答出";
          }
          para = document.createElement("p");
          node = document.createTextNode('------------');
          para.appendChild(node);
          this.answerHistory.appendChild(para);
          this.answerHistory.scrollTop = this.answerHistory.scrollHeight;
          return;
        }
        else if(msgs.command == "EndGame")
        {
          this.state = 0;
          para = document.createElement("p");
          node = document.createTextNode('本局结束');
          para.appendChild(node);
          this.answerHistory.appendChild(para);
          this.answerHistory.scrollTop = this.answerHistory.scrollHeight;
          return;
        }
        else if(msgs.command == "Guess")
        {
          para = document.createElement("p");
          node = document.createTextNode(msgs.content);
          para.appendChild(node);
          this.answerHistory.appendChild(para);
          this.answerHistory.scrollTop = this.answerHistory.scrollHeight;
        }
        else if(msgs.command == "Talk")
        {
          para = document.createElement("p");
          node = document.createTextNode(msgs.content);
          para.appendChild(node);
          this.chatHistory.appendChild(para);
          this.chatHistory.scrollTop = this.chatHistory.scrollHeight;
        }
        else if(msgs.command == "AnswerRight")
        {
          this.answerDisable = true;
          var mp3 = new Audio('/right.mp3');
          mp3.play();
          return ;
        }
        else if(msgs.command == "Hint")
        {
          if(msgs.content == "")
            return;
          para = document.createElement("p");
          node = document.createTextNode("提示:"+msgs.content);
          para.appendChild(node);
          this.answerHistory.appendChild(para);
          this.answerHistory.scrollTop = this.answerHistory.scrollHeight;
        }
        else if(msgs.command == "Draw")
        {
          switch(msgs.content.type){
            case "setColor":
              this.context.fillStyle = this.context.strokeStyle = msgs.content.color;
              break;
            case "setLine":
              this.context.lineWidth = msgs.content.lineWidth;
              this.context.fillStyle = this.context.strokeStyle = msgs.content.color;
              break;
            case "drawLine":
              this.context.beginPath();
              this.context.moveTo(msgs.content.beginX,msgs.content.beginY);
              this.context.lineTo(msgs.content.endX,msgs.content.endY);
              this.context.stroke();
              this.context.closePath();
              break;
            case "drawFilledCircle":
              this.context.beginPath();
              this.context.arc(msgs.content.X,msgs.content.Y,msgs.content.r,0,2*Math.PI);
              this.context.fill();
              this.context.closePath();
              break;
            case "drawCircle":
              this.context.beginPath();
              this.context.arc(msgs.content.X,msgs.content.Y,msgs.content.r,0,2*Math.PI);
              this.context.stroke();
              this.context.closePath();
              break;
            case "drawFilledRectangular":
              x1 = msgs.content.X1;
              y1 = msgs.content.Y1;
              x2 = msgs.content.X2;
              y2 = msgs.content.Y2;
              x = Math.min(x1,x2);
              y = Math.min(y1,y2);
              this.context.beginPath();
              this.context.fillRect(x,y,Math.abs(x1-x2),Math.abs(y1-y2));
              this.context.closePath();
              break;
            case "drawRectangular":
              x1 = msgs.content.X1;
              y1 = msgs.content.Y1;
              x2 = msgs.content.X2;
              y2 = msgs.content.Y2;
              x = Math.min(x1,x2);
              y = Math.min(y1,y2);
              this.context.beginPath();
              this.context.rect(x,y,Math.abs(x1-x2),Math.abs(y1-y2));
              this.context.stroke();
              this.context.closePath();
              break;
            case "clear":
              this.context.putImageData(this.blankImageData,0,0);
              break;
          }
        }
    });
    this.ws.addEventListener('close', ()=>{
        alert("连接断开");
    });
    
  },
}
</script>

<style>
  .turnEndContent{           
    position:absolute;      
    width:100%;
    height:100%;
    left:0;
    top:0;
    border-radius: 3%;
    background-color: lightgray;
  }

  #progress{
    position:absolute;
    left:10px;
    bottom:0;
    width:98%;
    height:5%;
    margin:0 auto;
}

  html,body,#app{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
  }

  #app{
    background:#0059ff url(/textura.png);
  }

  #container{
    width: 1200px;
    height: 100%;
    margin: 0px auto;
    padding: 0px;
  }

  #header{
    width:100%;
    height: 5%;
    color: #333;
  }

  #display{
    width:100%;
    height:90%;
    display: flex;
  }

  #aside {
    height: 100%;
    background-color: whitesmoke;
    color: #666363;
    margin-right: 20px;
    border-width:1px;
    border-radius: 3%;
    border-style:solid;
    border-color:#979797;
    flex: 5;
  }

  #drawTool{
    flex:3;
    background-color:#1E90FF;
    margin-right: 20px;
    border-radius: 2%;
    border-width:1px;
    border-style:solid;
    border-color: #979797;
    display:flex;
    flex-direction: column;
  }

  .toolLine{
    flex:1;
    display:flex;
    margin-bottom: 10px;
  }

  .colorLine{
    flex:2;
    display:flex;
  }

  .tool{
    flex:1;
    background-size:100% 100%;
    background-repeat: no-repeat;
  }

  #mainContainer{
    height: 100%;
    line-height: 0;
    display: flex;
    flex-direction: column;
    width:900px;
  }

  #canvasContainer{
    padding:0 0;
    border-radius: 3%;
    border-style:solid;
    border-color:#979797;
    background-color:#979797;
    margin-bottom: 20px;
    position: relative;
    overflow:hidden;
  } 

  
  #problem{
    position: absolute;
    width:20%;
    height: 10%;
    top:0%;
    left:50%;
    background-color:#1E90FF;
    z-index: 999;
    transform: translate(-50%,0);
    border-radius: 2%;
    border-style: solid;
    border-width: 1px;
    border-color: #979797;
    text-align: center;
    line-height: 50px;
    font-size:30px;
  }

  #myCanvas{
    width: 900px;
    height:500px;
    background-color: white;
    border-radius: 3%;
  }

  #startButton{
    position: relative;
    left:40%;
    top:60%;
    width:20%;
    height:20%;
    border-radius: 5%;
    font-size:100%;
  }

  #messageContainer{
    width: 100%;
    flex:2;
    display: flex;
    overflow:auto;
  }

  #colorChooser{
    position: relative;
    height:100%;
  }

  .answer,.chat{
    flex: 1;
    margin:0 0;
    border-width:1px;
    border-radius: 2% 2% 3px 3px;
    border-style:solid;
    border-color:#979797;
    background-color:#979797;
    overflow:hidden;
    display: flex;
    flex-direction: column;
